.SuperCheckbox_checkbox__1hDi9 {
    --background: #3311d3;
    --border: #d1d6ee;
    --border-hover: #bbc1e1;
    --border-active: #ff9b07;
    --tick: #fff;
    position: relative;
}
.SuperCheckbox_checkbox__1hDi9 input,
.SuperCheckbox_checkbox__1hDi9 svg {
    width: 21px;
    height: 21px;
}
.SuperCheckbox_checkbox__1hDi9 input:hover {
     --s: 2px;
     --b: var(--border-hover);
 }
.SuperCheckbox_checkbox__1hDi9 input {
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
    background: var(--background);
    border: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
    border-radius: 4px;
    transition: box-shadow 0.3s;
    box-shadow: inset 0 0 0 var(--s) var(--border);
    box-shadow: inset 0 0 0 var(--s) var(--b, var(--border));
}

.SuperCheckbox_checkbox__1hDi9 input:checked {
    --b: var(--border-active);
}
.SuperCheckbox_checkbox__1hDi9 svg {
    pointer-events: none;
    fill: none;
    stroke-width: 2px;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke:  var(--border-active);
    position: absolute;
    top: 0;
    left: 0;
    width: 21px;
    height: 21px;
    transform: scale(1) translateZ(0);
}
.SuperCheckbox_checkbox__1hDi9.SuperCheckbox_path__1ZMJK input:checked {
    --s: 2px;
    transition-delay: 0.4s;
}
.SuperCheckbox_checkbox__1hDi9.SuperCheckbox_path__1ZMJK input:checked + svg {
    --a: 16.1 86.12;
    --o: 102.22;
}
.SuperCheckbox_checkbox__1hDi9.SuperCheckbox_path__1ZMJK svg {
    stroke-dasharray: 86.12;
    stroke-dasharray: var(--a, 86.12);
    stroke-dashoffset: 86.12;
    stroke-dashoffset: var(--o, 86.12);
    transition: stroke-dasharray 0.6s, stroke-dashoffset 0.6s;
}


